<template>
	<div class="city">
		<app-header></app-header>
		<search :cities='city'></search>
		<dizhi :citys="citys"></dizhi>

	</div>
</template>

<script>
	import axios from 'axios'
	import head from '../components/city/header.vue'
	import search from '../components/city/search.vue'
	import dizhi from '../components/city/dizhi.vue'
	export default{
		data(){
			return{
				citys:{},
				city:{}
			}
		},
		mounted(){
			axios.get('./city.json').then((res) =>{
				this.citys = (res.data.data)
				this.city = (res.data.data.cities)
			})
		},
		components:{
		 	'app-header':head,
			search,
			dizhi
		}
	}
</script>

<style lang="scss">
	.city{
		width: 100%;
		height: 100vh;
		overflow: hidden;
	}
</style>